.roulette {
  position: relative;
  img {
    -webkit-filter: drop-shadow(2px 2px 0 black) drop-shadow(-2px -2px 0 black);
    filter: drop-shadow(1px 1px 0 black) drop-shadow(-1px -1px 0 black);
  }
  .bhdLno {
    max-width: calc(40vw - 10px);
    max-height: calc(40vw - 10px);
    overflow: hidden;
  }
}
